.n123{
display: none;
    border:0px solid white;
    width: 20%;
    height:50px;
    margin: right;
}

.nav1{
    display: grid;
    border:1px solid white;
    margin-left:80%;
    width:20%;
    height:500px;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    background-color: white;
    color: rgb(2, 2, 35);
    position: fixed;

}

.nav1>p:hover{
    background-color: azure;
    color:rgb(7, 7, 94);;
    border-radius:5px;
    padding:5px;
    background-color: aqua;

}
#nav111{
    background-color: rgb(4, 2, 31);
    color: white;
    border-radius: 0px;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    display: none;
}

.img1{
    width:30px;
    height:30px;
    border-radius:50%;
    text-align: center;
}
.nav2{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    width:20%;
    height:90%;

}
.nav1>p>a{
    color:white;
    text-decoration: none;
    color: rgb(1, 3, 31);
}
.nav1>p>a:hover{
    color:rgb(1, 1, 18);
}
#nav111>p>a{
    color:white;
    
}
@media only screen and (max-width:800px)  and (min-width:500px){
    #nav1{
        display:none;
    }
    .n123{
        display: block;
    }
  #nav111{
    display: grid;
    width:20%;
    text-align: center;
    border: none;
    box-shadow: none;
    margin-top: -10px;
  }
  .i123{
    width:35%
  }
  .nav1{
    border-radius: 0px;
  }
}